}

[class*="__container"] {
  max-width: 1170px;
  margin: 0 auto;
}

@media (width <= 1199.98px) {
  [class*="__container"] {
    max-width: 970px;
  }
}

@media (width <= 1199.98px) and (width <= 990.98px) {
  [class*="__container"] {
    max-width: 750px;
  }
}

@media (width <= 1199.98px) and (width <= 767.98px) {
  [class*="__container"] {
    max-width: none;
    padding: 0 .9375rem;
  }
}

.header {
