  line-height: 26px;
}

.item-how:not(:last-child):after {
  content: "";
  background: url("line709.061a3557.svg") 0 0 / 100% no-repeat;
  width: 210px;
  height: .6875rem;
  position: absolute;
  top: 2.5625rem;
  right: 0;
  transform: translate(86.5%);
}

.item-how_2:not(:last-child):after {
  transform: translate(86.5%)rotate(-180deg);
}

.item-how_1 .item-how__icon {
  background: #9672ff;
}

.item-how_2 .item-how__icon {
  background: #4ddffd;
}

.item-how_3 .item-how__icon {
  background: #f2b8ec;
}

