  .body-main-block {
    display: block;
  }

  .form-main-block {
    margin-right: 0;
  }

  .menu__list {
    justify-content: center;
  }

  .menu__link {
    font-size: 1.125rem;
  }

  .partners-main-block {
    z-index: 2;
    max-width: none;
    position: relative;
  }

  .main-block__body {
    margin-bottom: 2.5rem;
  }

  .main-block__container {
    padding-top: 6.25rem;
    padding-bottom: 1.875rem;
  }

  .content-main-block__body {
    padding-right: 0;
  }

  .item-how:not(:last-child):after {
    width: 10.625rem;
    transform: translate(69.5%);
  }

  .item-how_2:not(:last-child):after {
    transform: translate(69.5%)rotate(-180deg);
  }
}

@media (width <= 990.98px) {
  .main-block {
    padding-bottom: 4.375rem;
  }

  .item-how:not(:last-child):after {
    width: 7.8125rem;
    transform: translate(64%);
  }

  .item-how_2:not(:last-child):after {
    transform: translate(64%)rotate(-180deg);
  }
}

@media (width <= 767.98px) {
  .main-block {
    padding-bottom: 1.875rem;
  }

  .partners-main-block__items {
    justify-content: center;
  }

  .content-main-block__title {
    font-size: 1.875rem;
    line-height: 120%;
  }

  .form-main-block {
    display: block;
  }

  .form-main-block__button {
