.reviews {
    padding-top: 6.875rem;
    padding-bottom: 6.875rem;

    @media (max-width: 990.98px) {
        .reviews {
            padding-top: 4.375rem;
            padding-bottom: 4.375rem;
        }
    }
    @media (max-width: 767.98px) {
        .reviews {
            padding-top: 1.875rem;
            padding-bottom: 1.875rem;
        }
    }

    &__title {
        margin-bottom: 3.125rem;
        max-width: 35.625rem;
    }

    &__items {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
        gap: 1.875rem;
    }
}

.item-reviews {
    color: inherit;
    background: #ffff;
    border: 0.0625rem solid #ebebeb;
    border-radius: 0.625rem;
    padding: 3.125rem 40px;
    transition: all 0.3s ease 0s;

    .item-reviews:hover {
        border: 1px solid transparent;
        box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.03),
            0px 64.8148px 46.8519px rgba(0, 0, 0, 0.0227778),
            0px 38.5185px 25.4815px rgba(0, 0, 0, 0.0182222),
            0px 20px 13px rgba(0, 0, 0, 0.015),
            0px 8.14815px 6.51852px rgba(0, 0, 0, 0.0117778),
            0px 1.85185px 3.14815px rgba(0, 0, 0, 0.00722222),
            3px 44px 54px rgba(0, 0, 0, 0.08);
    }
    .item-reviews__text {
        line-height: 1.5;
        margin-bottom: 3.125rem;
    }
}

.author-item-reviews {
    gap: 1.25rem;
    display: flex;
    align-items: center;
}
.author-item-reviews__avatar {
    flex: 0 0 2.5rem;
    border-radius: 50%;
}

.author-item-reviews__name {
    font-weight: 700;
    line-height: 1.375rem;
}
.author-item-reviews__location {
    color: #66666666;
    font-size: 0.875rem;
    line-height: 1.1875rem;
}

/* ====================================== */

.reviews__footer {
    padding-top: 5.3125rem;
}
.footer-reviews {
    display: flex;
    gap: 6.25rem;
}
@media (max-width: 991.98px) {
    .footer-reviews {
        flex-wrap: wrap;
        row-gap: 1.875rem;
    }
}
.footer-reviews__partners {
    flex: 1 1 auto;
}
.partners-footer-reviews {
    display: flex;
    flex-wrap: wrap;
    gap: 1.25rem;
    align-items: center;

    justify-content: space-between;
}

.footer-reviews__info {
    flex: 0 0 18.75rem;
    line-height: 1.5;
}
.footer-reviews__info a {
    color: #2cbde7;
    text-decoration: underline;
}
.footer-reviews__info a:hover {
    text-decoration: none;
}

.simple-rating {
    position: relative;
    font-size: 1.5rem;
    display: inline-block;
}
.simple-rating::before {
    content: "★★★★★";
    display: block;
}

.simple-rating__items {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row-reverse;
    overflow: hidden;
}

.simple-rating__item {
    position: absolute;
    width: 0%;
    height: 0%;
    opacity: 0;
    visibility: hidden;
    top: 0;
    left: 0;
}

.simple-rating__label {
    flex: 0 0 20%;
    height: 100%;
    cursor: pointer;
    color: #f0dbbb;
}
.simple-rating__label::before {
    content: "★";
    display: block;
    transition: color 0.3s ease 0s;
}
.simple-rating__item:checked,
.simple-rating__item:checked ~ .simple-rating__label {
    color: #fbb040;
}

.simple-rating__label:hover,
.simple-rating__label:hover ~ .simple-rating__label,
.simple-rating__label:checked ~ .simple-rating__label:hover {
    color: #ffef9f;
}
